// components/post/post-card.tsx
"use client";

import type React from "react";

import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import Link from "next/link";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Button } from "@/components/ui/button";
import { Heart, MessageCircle, LogIn, Users, MoreVertical } from "lucide-react";
import { createClient } from "@/lib/supabase/client";
import { formatDistanceToNow } from "date-fns";
import { FollowButton } from "@/components/follow/follow-button";
import { LikesList } from "@/components/post/likes-list";

interface Post {
  id: string;
  content: string;
  created_at: string;
  user_id: string;
  username: string;
  display_name: string;
  avatar_url: string | null;
  likes_count: number;
  comments_count: number;
  user_has_liked: boolean;
}

interface PostCardProps {
  post: Post;
  showFollowButton?: boolean;
  currentUserId?: string;
  isCurrentUserFollowing?: boolean;
  onFollowChange?: (userId: string, isFollowing: boolean) => void;
}

export function PostCard({ 
  post, 
  showFollowButton = false,
  currentUserId,
  isCurrentUserFollowing = false,
  onFollowChange
}: PostCardProps): React.JSX.Element {
  const router = useRouter();
  const [liked, setLiked] = useState<boolean>(post.user_has_liked);
  const [likesCount, setLikesCount] = useState<number>(post.likes_count);
  const [loading, setLoading] = useState<boolean>(false);
  const [isAuthenticated, setIsAuthenticated] = useState<boolean>(false);
  const [authUserId, setAuthUserId] = useState<string | null>(null);
  const [showLikesDialog, setShowLikesDialog] = useState<boolean>(false);

  // Provjera da li je korisnik logovan
  useEffect(() => {
    const checkAuth = async (): Promise<void> => {
      const supabase = createClient();
      const { data: { user } } = await supabase.auth.getUser();
      setIsAuthenticated(!!user);
      setAuthUserId(user?.id || null);
      
      // Ažuriraj liked status ako je korisnik logovan
      if (user) {
        const { data: like } = await supabase
          .from("likes")
          .select("id")
          .eq("post_id", post.id)
          .eq("user_id", user.id)
          .maybeSingle();
        setLiked(!!like);
      }
    };
    
    void checkAuth();
  }, [post.id]);

  // Trenutni korisnik (prioritet: prop > state)
  const currentUser = currentUserId || authUserId;

  // Da li prati ovog korisnika
  const isFollowing = isCurrentUserFollowing || false;

  const handleLike = async (e: React.MouseEvent<HTMLButtonElement>): Promise<void> => {
    e.preventDefault();
    e.stopPropagation();

    // Ako nije logovan, redirect na login
    if (!isAuthenticated) {
      router.push("/login");
      return;
    }

    setLoading(true);
    const supabase = createClient();

    const {
      data: { user },
    } = await supabase.auth.getUser();

    if (!user) {
      router.push("/login");
      return;
    }

    try {
      if (liked) {
        await supabase
          .from("likes")
          .delete()
          .eq("post_id", post.id)
          .eq("user_id", user.id);
        setLiked(false);
        setLikesCount((prev) => Math.max(0, prev - 1));
      } else {
        await supabase
          .from("likes")
          .insert({ 
            post_id: post.id, 
            user_id: user.id 
          });
        setLiked(true);
        setLikesCount((prev) => prev + 1);
      }
    } catch (error) {
      console.error("Error toggling like:", error);
    } finally {
      setLoading(false);
      // Refresh feed da bi se ažurirali broječi
      router.refresh();
    }
  };

  const handleFollowStatusChange = (isFollowing: boolean) => {
    if (onFollowChange && post.user_id) {
      onFollowChange(post.user_id, isFollowing);
    }
  };

  const handleCardClick = (e: React.MouseEvent<HTMLDivElement>): void => {
    // Provjeri da li je klik na link ili dugme
    const target = e.target as HTMLElement;
    if (target.closest("a, button, [role='button']")) {
      return;
    }
    router.push(`/post/${post.id}`);
  };

  const handleCommentButtonClick = (e: React.MouseEvent<HTMLButtonElement>): void => {
    e.preventDefault();
    e.stopPropagation();
    
    // Ako nije logovan, redirect na login
    if (!isAuthenticated) {
      router.push("/login");
      return;
    }
    
    router.push(`/post/${post.id}`);
  };

  const handleShowLikes = (e: React.MouseEvent<HTMLButtonElement>) => {
    e.preventDefault();
    e.stopPropagation();
    
    // Ako nema lajkova, ne otvaraj dijalog
    if (likesCount === 0) return;
    
    setShowLikesDialog(true);
  };

  const handleProfileLinkClick = (e: React.MouseEvent<HTMLAnchorElement>): void => {
    e.stopPropagation();
  };

  const getInitials = (name: string): string => {
    if (!name) return "??";
    return name
      .split(" ")
      .map((part) => part[0])
      .join("")
      .toUpperCase()
      .slice(0, 2);
  };

  // Mobile-friendly formatiranje datuma
  const formatDateMobile = (dateString: string) => {
    const date = new Date(dateString);
    const now = new Date();
    const diffInHours = (now.getTime() - date.getTime()) / (1000 * 60 * 60);
    
    if (diffInHours < 1) {
      const diffInMinutes = diffInHours * 60;
      if (diffInMinutes < 1) {
        return "now";
      }
      return `${Math.floor(diffInMinutes)}m`;
    } else if (diffInHours < 24) {
      return `${Math.floor(diffInHours)}h`;
    } else if (diffInHours < 168) { // 7 days
      return `${Math.floor(diffInHours / 24)}d`;
    } else {
      return date.toLocaleDateString("en-US", { 
        month: "short", 
        day: "numeric" 
      });
    }
  };

  return (
    <>
      <div 
        onClick={handleCardClick}
        className="block transition-colors hover:bg-muted/50 active:bg-muted/30 cursor-pointer rounded-lg relative"
        role="button"
        tabIndex={0}
        onKeyDown={(e: React.KeyboardEvent<HTMLDivElement>) => {
          if (e.key === "Enter" || e.key === " ") {
            e.preventDefault();
            router.push(`/post/${post.id}`);
          }
        }}
      >
        <article className="p-4 md:p-6">
          <div className="flex gap-3 md:gap-4">
            {/* Avatar link - veći na mobile */}
            <div className="relative z-10 flex-shrink-0">
              <Link 
                href={`/profile/${post.username}`} 
                onClick={handleProfileLinkClick}
                className="inline-block"
                aria-label={`View ${post.display_name}'s profile`}
              >
                <Avatar className="h-12 w-12 md:h-10 md:w-10">
                  <AvatarImage 
                    src={post.avatar_url || undefined} 
                    alt={post.display_name} 
                    className="object-cover"
                  />
                  <AvatarFallback className="text-sm md:text-xs">
                    {getInitials(post.display_name)}
                  </AvatarFallback>
                </Avatar>
              </Link>
            </div>

            <div className="flex-1 min-w-0 space-y-2 md:space-y-3">
              {/* User info and timestamp */}
              <div className="flex items-start justify-between">
                <div className="flex-1 min-w-0">
                  <div className="flex flex-col sm:flex-row sm:items-center gap-1 sm:gap-2 flex-wrap">
                    <div className="flex items-center gap-2">
                      <Link
                        href={`/profile/${post.username}`}
                        onClick={handleProfileLinkClick}
                        className="font-semibold hover:underline focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring text-sm md:text-base truncate max-w-[120px] md:max-w-none"
                        aria-label={`View ${post.display_name}'s profile`}
                      >
                        {post.display_name}
                      </Link>
                      
                      {/* Follow button inline za mobile */}
                      {showFollowButton && isAuthenticated && currentUser && currentUser !== post.user_id && (
                        <div className="md:hidden relative z-20" onClick={(e) => e.stopPropagation()}>
                          <FollowButton
                            followerId={currentUser}
                            followingId={post.user_id}
                            isFollowing={isFollowing}
                            onFollowChange={handleFollowStatusChange}
                            size="sm"
                          />
                        </div>
                      )}
                    </div>
                    
                    <div className="flex items-center gap-1 md:gap-2 text-xs md:text-sm text-muted-foreground">
                      <Link
                        href={`/profile/${post.username}`}
                        onClick={handleProfileLinkClick}
                        className="hover:underline truncate max-w-[100px] md:max-w-none"
                        aria-label={`View @${post.username}'s profile`}
                      >
                        @{post.username}
                      </Link>
                      
                      <span className="hidden sm:inline">·</span>
                      
                      <span className="whitespace-nowrap" title={formatDistanceToNow(new Date(post.created_at), { addSuffix: true })}>
                        {/* Mobile-friendly date format */}
                        <span className="sm:hidden">{formatDateMobile(post.created_at)}</span>
                        <span className="hidden sm:inline">
                          {formatDistanceToNow(new Date(post.created_at), { addSuffix: true })}
                        </span>
                      </span>
                    </div>
                  </div>
                </div>

                {/* Follow button (desktop) i menu za mobile */}
                <div className="flex items-center gap-2">
                  {/* Follow button (desktop) */}
                  {showFollowButton && isAuthenticated && currentUser && currentUser !== post.user_id && (
                    <div className="hidden md:block relative z-20" onClick={(e) => e.stopPropagation()}>
                      <FollowButton
                        followerId={currentUser}
                        followingId={post.user_id}
                        isFollowing={isFollowing}
                        onFollowChange={handleFollowStatusChange}
                      />
                    </div>
                  )}
                  
                  {/* Menu button za mobile */}
                  <button 
                    className="md:hidden p-1 text-muted-foreground hover:text-foreground"
                    onClick={(e) => {
                      e.preventDefault();
                      e.stopPropagation();
                      // Možete dodati funkcionalnost za menu ovdje
                    }}
                    aria-label="More options"
                  >
                    <MoreVertical className="h-4 w-4" />
                  </button>
                </div>
              </div>

              {/* Post content */}
              <p className="whitespace-pre-wrap text-pretty break-words text-sm md:text-base leading-relaxed">
                {post.content}
              </p>

              {/* Actions - mobile optimizovane */}
              <div className="flex items-center justify-between md:justify-start md:gap-6 pt-2 md:pt-3 border-t md:border-t-0">
                {/* Comment button */}
                <Button
                  variant="ghost"
                  size="sm"
                  className="h-9 md:h-8 gap-1.5 md:gap-2 px-2 md:px-3 text-muted-foreground hover:text-primary hover:bg-transparent active:scale-95 transition-transform"
                  onClick={handleCommentButtonClick}
                  type="button"
                  aria-label={`${isAuthenticated ? `View comments (${post.comments_count})` : 'Sign in to comment'}`}
                >
                  <MessageCircle className="h-4 w-4 md:h-4 md:w-4" />
                  <span className="text-xs md:text-sm font-medium">{post.comments_count}</span>
                  {!isAuthenticated && (
                    <span className="absolute -top-1 -right-1">
                      <LogIn className="h-2.5 w-2.5 md:h-3 md:w-3 text-muted-foreground" />
                    </span>
                  )}
                </Button>

                {/* Like section */}
                <div className="flex items-center">
                  {/* Like button */}
                  <Button
                    variant="ghost"
                    size="sm"
                    className={`h-9 md:h-8 gap-1.5 md:gap-2 px-2 md:px-3 relative z-10 hover:bg-transparent active:scale-95 transition-transform ${
                      liked ? "text-red-500 hover:text-red-600" : "text-muted-foreground hover:text-red-500"
                    }`}
                    onClick={handleLike}
                    disabled={loading || (!isAuthenticated && !liked)}
                    type="button"
                    aria-label={`${isAuthenticated ? (liked ? "Unlike" : "Like") : "Sign in to like"} post`}
                  >
                    <Heart className={`h-4 w-4 md:h-4 md:w-4 ${liked ? "fill-current" : ""}`} />
                  </Button>
                  
                  {/* Like count button - touch friendly */}
                  <button
                    onClick={handleShowLikes}
                    disabled={likesCount === 0}
                    className={`h-9 md:h-8 px-2 md:px-3 text-xs md:text-sm font-medium rounded-md transition-all active:scale-95 min-w-[44px] ${
                      likesCount > 0 
                        ? "hover:bg-muted/50 cursor-pointer text-foreground" 
                        : "text-muted-foreground cursor-default"
                    }`}
                    title={likesCount > 0 ? `View ${likesCount} like${likesCount !== 1 ? 's' : ''}` : "No likes yet"}
                  >
                    {likesCount}
                    {likesCount > 0 && (
                      <span className="absolute -top-0.5 -right-0.5 md:-top-1 md:-right-1">
                        <Users className="h-2.5 w-2.5 md:h-3 md:w-3 text-primary/60" />
                      </span>
                    )}
                  </button>
                </div>

                {/* Share button za mobile */}
                <button
                  className="md:hidden p-2 text-muted-foreground hover:text-foreground"
                  onClick={(e) => {
                    e.preventDefault();
                    e.stopPropagation();
                    // Dodajte share funkcionalnost
                  }}
                  aria-label="Share post"
                >
                  <svg className="h-4 w-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8.684 13.342C8.886 12.938 9 12.482 9 12c0-.482-.114-.938-.316-1.342m0 2.684a3 3 0 110-2.684m0 2.684l6.632 3.316m-6.632-6l6.632-3.316m0 0a3 3 0 105.367-2.684 3 3 0 00-5.367 2.684zm0 9.316a3 3 0 105.368 2.684 3 3 0 00-5.368-2.684z" />
                  </svg>
                </button>

                {/* Indikator da treba login */}
                {!isAuthenticated && (
                  <span className="hidden md:inline text-xs text-muted-foreground">
                    Sign in to interact
                  </span>
                )}
              </div>
              
              {/* Mobile login prompt */}
              {!isAuthenticated && (
                <div className="md:hidden mt-3 p-2 bg-muted/30 rounded-lg">
                  <p className="text-xs text-muted-foreground text-center">
                    <button 
                      onClick={() => router.push("/login")}
                      className="text-primary font-medium hover:underline"
                    >
                      Sign in
                    </button>{" "}
                    to like and comment
                  </p>
                </div>
              )}
            </div>
          </div>
        </article>
      </div>

      {/* Likes List Dialog */}
      <LikesList
        postId={post.id}
        open={showLikesDialog}
        onOpenChange={setShowLikesDialog}
      />
    </>
  );
}